<template>
    <div class="ui container">
        <div class="vuetable-pagination ui basic segment grid">
            <vuetable-pagination-info ref="paginationInfoTop"></vuetable-pagination-info>
            <vuetable-pagination ref="paginationTop"
                @vuetable-pagination:change-page="onChangePage">
            </vuetable-pagination>
        </div>
        <vuetable ref="vuetable" api-url="http://localhost:3004/user"
            :fields = "fields"
            :per-page="1"
            pagination-path="" 
            @vuetable:pagination-data="onPaginationData">
        </vuetable>
    </div>
</template>

<script type="text/javascript">
    import Vuetable from 'vuetable-2/src/components/Vuetable'
    import VuetablePagination from 'vuetable-2/src/components/VuetablePagination'
    import VuetablePaginationInfo from 'vuetable-2/src/components/VuetablePaginationInfo'
    import Vue from 'vue'
    import CustomActions from './CustomActions'
    Vue.component('custom-actions', CustomActions)

    export default {
        components: {
            Vuetable,
            VuetablePagination,
            VuetablePaginationInfo,
        },
        data(){
            return {
                fields: [
                    {
                        name: "__sequence",
                        title: "#",
                        titleClass: 'center aligned',
                        dataClass: 'right aligned'
                    },
                    'hostname', 
                    {
                        name: 'ipaddr',
                        title: "IP地址",
                    },
                    {
                        name: 'ipmi_ipaddr',
                        title: "IPMI IP地址"
                    },
                    {
                        name: 'group_name',
                        title: "组名",
                        callback: 'groupLabel'
                    },
                    {
                        name: '__component:custom-actions',
                        title: '动作',
                        titleClass: 'center aligned',
                        dataClass: 'center aligned'
                    }
                ]
            }
        },
        methods: {
            groupLabel(value) {
                return value == 'management'
                    ? '<span class="label label-info"><i class="glyphicon glyphicon-star"></i> 管理组 </span>'
                    : '<span class="label label-danger"><i class="glyphicon glyphicon-heart" ></i> 计算组 </span>'
            },
            onPaginationData(paginationData) {
                this.$refs.paginationTop.setPaginationData(paginationData)
                this.$refs.paginationInfoTop.setPaginationData(paginationData)
            },
            onChangePage(page) {
                this.$refs.vuetable.changePage(page)
            }
        }
    }
</script>



