<template>
    <div class="custom-actions">
        <button class="ui basic button" @click="itemAction('view-item', rowData, rowIndex)"><i class="add icon"></i></button>
        <button class="ui basic button" @click="itemAction('edit-item', rowData, rowIndex)"><i class="edit icon"></i></button>
        <button class="ui basic button" @click="itemAction('delete-item', rowData, rowIndex)"><i class="delete icon"></i></button>
    </div>
</template>

<script type="text/javascript">
    export default {
        name: 'custom-actions',
        props: {
            rowData: {
                type: Object,
                required: true
            },
            rowIndex: {
                type: Number
            }
        },
        methods: {
            itemAction(action, data, index) {
                console.log()
            }
        }
    }
</script>

<style type="text/css">
    .custom-actions button.ui.button {
      padding: 8px 8px;
    }
    .custom-actions button.ui.button > i.icon {
      margin: auto !important;
    }
</style>


